import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { 
  LayoutDashboard, 
  Users, 
  FileText, 
  CreditCard, 
  LogOut,
  Heart
} from 'lucide-react';

const Sidebar = ({ onLogout }) => {
  const location = useLocation();

  // Ensure onLogout is defined
  const handleLogout = onLogout || (() => {
    console.warn('onLogout is not defined in Sidebar');
    localStorage.removeItem('admin');
    window.location.href = '/login';
  });

  const menuItems = [
    {
      name: 'Dashboard',
      path: '/dashboard',
      icon: LayoutDashboard
    },
    {
      name: 'Employees',
      path: '/employees',
      icon: Users
    },
    {
      name: 'Invoices',
      path: '/invoices',
      icon: FileText
    },
    {
      name: 'Monitor Payment',
      path: '/payments',
      icon: CreditCard
    }
  ];

  const isActive = (path) => {
    return location.pathname === path;
  };

  return (
    <div className="flex flex-col h-full">
      {/* Logo */}
      <div className="flex items-center justify-center h-16 px-4 bg-navy-800">
        <div className="flex items-center space-x-2">
          <Heart className="h-8 w-8 text-pink-400" />
          <span className="text-xl font-bold text-white">Fur-Ever Care</span>
        </div>
      </div>

      {/* Navigation */}
      <nav className="flex-1 px-4 py-6 space-y-2">
        {menuItems.map((item) => {
          const Icon = item.icon;
          return (
            <Link
              key={item.name}
              to={item.path}
              className={`flex items-center px-4 py-3 text-sm font-medium rounded-lg transition-colors duration-200 ${
                isActive(item.path)
                  ? 'bg-blue-600 text-white'
                  : 'text-gray-300 hover:bg-navy-800 hover:text-white'
              }`}
            >
              <Icon className="h-5 w-5 mr-3" />
              {item.name}
            </Link>
          );
        })}
      </nav>

      {/* Logout button */}
      <div className="px-4 py-6 border-t border-navy-700">
        <button
          onClick={handleLogout}
          className="flex items-center w-full px-4 py-3 text-sm font-medium text-gray-300 rounded-lg hover:bg-navy-800 hover:text-white transition-colors duration-200"
        >
          <LogOut className="h-5 w-5 mr-3" />
          Logout
        </button>
      </div>
    </div>
  );
};

export default Sidebar;
