import React, { useState, useEffect } from 'react';
import { dashboardAPI } from '../services/api';
import SalesMetrics from '../components/dashboard/SalesMetrics';
import SalesTrend from '../components/dashboard/SalesTrend';
import ProductsRevenue from '../components/dashboard/ProductsRevenue';
import DoctorStatistics from '../components/dashboard/DoctorStatistics';
import InventoryCost from '../components/dashboard/InventoryCost';
import DoctorSurgeryFees from '../components/dashboard/DoctorSurgeryFees';
import RecentPayments from '../components/dashboard/RecentPayments';

const Dashboard = () => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [dashboardData, setDashboardData] = useState({
    salesMetrics: null,
    salesTrend: [],
    productsRevenue: [],
    doctorStatistics: [],
    inventoryCost: null,
    doctorSurgeryFees: [],
    recentPayments: []
  });

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      setLoading(true);
      setError('');

      const [
        salesMetricsRes,
        salesTrendRes,
        productsRevenueRes,
        doctorStatisticsRes,
        inventoryCostRes,
        doctorSurgeryFeesRes,
        recentPaymentsRes
      ] = await Promise.all([
        dashboardAPI.getSalesMetrics().catch(err => ({ data: { success: false, data: null, error: err } })),
        dashboardAPI.getSalesTrend(6).catch(err => ({ data: { success: false, data: [], error: err } })),
        dashboardAPI.getProductsRevenue().catch(err => ({ data: { success: false, data: [], error: err } })),
        dashboardAPI.getDoctorStatistics().catch(err => ({ data: { success: false, data: [], error: err } })),
        dashboardAPI.getInventoryCost().catch(err => ({ data: { success: false, data: null, error: err } })),
        dashboardAPI.getDoctorSurgeryFees().catch(err => ({ data: { success: false, data: [], error: err } })),
        dashboardAPI.getRecentPayments(5).catch(err => ({ data: { success: false, data: [], error: err } }))
      ]);

      // Extract data safely, handling both success and error responses
      setDashboardData({
        salesMetrics: salesMetricsRes?.data?.success ? (salesMetricsRes?.data?.data || null) : null,
        salesTrend: salesTrendRes?.data?.success ? (salesTrendRes?.data?.data || []) : [],
        productsRevenue: productsRevenueRes?.data?.success ? (productsRevenueRes?.data?.data || []) : [],
        doctorStatistics: doctorStatisticsRes?.data?.success ? (doctorStatisticsRes?.data?.data || []) : [],
        inventoryCost: inventoryCostRes?.data?.success ? (inventoryCostRes?.data?.data || null) : null,
        doctorSurgeryFees: doctorSurgeryFeesRes?.data?.success ? (doctorSurgeryFeesRes?.data?.data || []) : [],
        recentPayments: recentPaymentsRes?.data?.success ? (recentPaymentsRes?.data?.data || []) : []
      });
    } catch (error) {
      console.error('Error fetching dashboard data:', error);
      setError(error.response?.data?.message || error.message || 'Failed to load dashboard data. Please check if XAMPP is running.');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-lg p-6 text-center">
        <p className="text-red-700">{error}</p>
        <button
          onClick={fetchDashboardData}
          className="mt-4 px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700"
        >
          Retry
        </button>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Sales Analytics Dashboard</h1>
          <p className="text-gray-600 mt-2">Monitor your clinic's financial performance</p>
        </div>
        <button
          onClick={fetchDashboardData}
          disabled={loading}
          className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed flex items-center space-x-2"
        >
          <span>{loading ? 'Refreshing...' : 'Refresh'}</span>
        </button>
      </div>

      {/* Sales Metrics */}
      <SalesMetrics data={dashboardData.salesMetrics} />

      {/* Charts Grid - First Row */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Sales Trend */}
        <div className="lg:col-span-1 bg-white rounded-lg shadow-md border border-gray-200 p-6">
          <SalesTrend data={dashboardData.salesTrend} />
        </div>

        {/* Products Revenue */}
        <div className="lg:col-span-1 bg-white rounded-lg shadow-md border border-gray-200 p-6">
          <ProductsRevenue data={dashboardData.productsRevenue} />
        </div>

        {/* Doctor Statistics */}
        <div className="lg:col-span-1 bg-white rounded-lg shadow-md border border-gray-200 p-6">
          <DoctorStatistics data={dashboardData.doctorStatistics} />
        </div>
      </div>

      {/* Doctor Surgery Fees - Full Width */}
      <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
        <DoctorSurgeryFees data={dashboardData.doctorSurgeryFees} />
      </div>

      {/* Inventory and Recent Payments - Side by Side */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
          <InventoryCost data={dashboardData.inventoryCost} />
        </div>

        <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
          <RecentPayments data={dashboardData.recentPayments} />
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
